'use client';

import React, { useState } from 'react';
import useSWR from 'swr';
import {
    UserPlus, Search, Star, Clock, BadgeCheck,
    Edit2, Gift, Banknote, Receipt, Settings,
    Phone, Calendar
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

export default function MembersPage() {
    const [selectedId, setSelectedId] = useState<number | null>(null);
    const [search, setSearch] = useState('');

    // Fetch Members
    const { data: membersData, mutate: refreshMembers } = useSWR(
        `/api/members?q=${search}`,
        fetcher
    );

    const members = membersData?.data || [];
    const selectedMember = members.find((m: any) => m.id === selectedId) || members[0];

    // Set initial selection if needed
    React.useEffect(() => {
        if (!selectedId && members.length > 0) {
            setSelectedId(members[0].id);
        }
    }, [members, selectedId]);

    const handleCreate = async () => {
        const name = prompt('Full Name:');
        if (!name) return;
        const phone = prompt('Phone:');
        if (!phone) return;

        await fetch('/api/members/create', {
            method: 'POST',
            body: JSON.stringify({ fullName: name, phone })
        });
        refreshMembers();
    };

    const getTierColor = (tier: string) => {
        switch (tier) {
            case 'GOLD': return 'text-[#eab308] bg-[#eab308]/20 border-[#eab308]/30';
            case 'SILVER': return 'text-slate-300 bg-slate-700 border-slate-600';
            case 'BRONZE': return 'text-[#f97316] bg-[#f97316]/20 border-[#f97316]/30';
            default: return 'text-slate-500 bg-slate-800 border-slate-700';
        }
    };

    return (
        <div className="flex flex-1 overflow-hidden relative h-full">
            {/* Sidebar List */}
            <aside className="w-[400px] bg-[#0f1a14] border-r border-[#1e3328] flex flex-col shrink-0 z-10">
                <div className="p-4 border-b border-[#1e3328] space-y-4">
                    <div className="flex justify-between items-center">
                        <h3 className="font-bold text-lg text-white">Membership</h3>
                        <button
                            onClick={handleCreate}
                            className="flex items-center gap-2 bg-[#22c55e] hover:bg-[#16a34a] text-black text-sm font-bold py-1.5 px-3 rounded transition-colors"
                        >
                            <UserPlus size={18} /> New
                        </button>
                    </div>
                    <div className="relative group">
                        <span className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-500 group-focus-within:text-[#22c55e]">
                            <Search size={20} />
                        </span>
                        <input
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded py-2 pl-10 pr-4 text-sm text-white focus:ring-1 focus:ring-[#22c55e] focus:border-[#22c55e] placeholder:text-slate-600 outline-none"
                            placeholder="Search name, ID or phone..."
                            type="text"
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                        />
                    </div>
                </div>

                <div className="flex-1 overflow-y-auto p-3 space-y-2">
                    {members.map((member: any) => (
                        <div
                            key={member.id}
                            onClick={() => setSelectedId(member.id)}
                            className={`group flex gap-3 p-3 border rounded cursor-pointer relative overflow-hidden transition-all
                  ${selectedId === member.id
                                    ? 'bg-[#16261d] border-[#22c55e]/50'
                                    : 'bg-[#050a07] border-[#1e3328] hover:bg-[#16261d] hover:border-[#1e3328]/80'
                                }
                `}
                        >
                            {selectedId === member.id && <div className="absolute left-0 top-0 bottom-0 w-1 bg-[#22c55e]"></div>}
                            <div className="size-12 rounded bg-slate-800 shrink-0 flex items-center justify-center font-bold text-slate-500 border border-[#1e3328]">
                                {member.fullName.substring(0, 2).toUpperCase()}
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex justify-between items-start mb-1">
                                    <h4 className={`font-bold text-sm truncate ${selectedId === member.id ? 'text-white' : 'text-slate-300 group-hover:text-white'}`}>
                                        {member.fullName}
                                    </h4>
                                    <span className={`text-[10px] font-bold px-1.5 py-0.5 rounded border ${getTierColor(member.tier)}`}>
                                        {member.tier}
                                    </span>
                                </div>
                                <p className="text-xs text-slate-500 mb-2">ID: {member.memberCode}</p>
                                <div className="flex items-center gap-4 text-xs">
                                    <span className={`flex items-center gap-1 ${selectedId === member.id ? 'text-[#22c55e]' : 'text-slate-400'}`}>
                                        <Star size={14} /> {member.pointsBalance.toLocaleString()} Pts
                                    </span>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </aside>

            {/* Main Detail View */}
            {selectedMember ? (
                <main className="flex-1 flex flex-col h-full bg-[#050a07] overflow-hidden relative">
                    {/* Header Gradient Background */}
                    <div className="absolute top-0 left-0 right-0 h-48 bg-gradient-to-b from-[#0f1a14] to-[#050a07] z-0 border-b border-[#1e3328]"></div>

                    {/* Profile Header */}
                    <div className="relative z-10 px-8 pt-8 pb-6 flex items-end justify-between">
                        <div className="flex items-end gap-6">
                            <div className="size-32 rounded-lg border-4 border-[#050a07] bg-slate-800 flex items-center justify-center shadow-lg">
                                <span className="text-4xl font-bold text-slate-500">{selectedMember.fullName.substring(0, 2).toUpperCase()}</span>
                            </div>
                            <div className="pb-2">
                                <div className="flex items-center gap-3 mb-1">
                                    <h1 className="text-3xl font-bold text-white">{selectedMember.fullName}</h1>
                                    <BadgeCheck size={24} className="text-[#eab308]" />
                                </div>
                                <div className="flex items-center gap-4 text-sm text-slate-400 mb-2">
                                    <span className="font-mono text-white">{selectedMember.memberCode}</span>
                                    <span className="w-1 h-1 bg-slate-600 rounded-full"></span>
                                    <span className="flex items-center gap-1"><Phone size={14} /> {selectedMember.phone}</span>
                                    <span className="w-1 h-1 bg-slate-600 rounded-full"></span>
                                    <span className="flex items-center gap-1"><Calendar size={14} /> Joined {new Date(selectedMember.joinDate).toLocaleDateString()}</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Stats Grid */}
                    <div className="relative z-10 px-8 mb-6">
                        <div className="grid grid-cols-4 gap-4">
                            <div className="bg-[#0f1a14] border border-[#1e3328] p-4 rounded flex flex-col">
                                <span className="text-slate-500 text-xs uppercase font-bold tracking-wider mb-1">Points Balance</span>
                                <div className="flex items-end justify-between">
                                    <span className="text-3xl font-bold text-[#22c55e]">{selectedMember.pointsBalance.toLocaleString()}</span>
                                    <Star className="text-[#22c55e]/30" size={32} />
                                </div>
                            </div>
                        </div>
                        {/* Future: Transaction History Table */}
                        <div className="mt-8">
                            <h3 className="text-lg font-bold text-white mb-4">Activity History</h3>
                            <div className="p-8 border border-[#1e3328] rounded-xl bg-[#0f1a14] text-center text-slate-500">
                                <p>No recent activity found.</p>
                            </div>
                        </div>
                    </div>
                </main>
            ) : (
                <main className="flex-1 flex items-center justify-center text-slate-500">
                    Select a member to view details
                </main>
            )}
        </div>
    );
}
